.frameContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-16xl-2);
  min-width: 239px;
  max-width: 100%;
}
.subject {
  flex: 1;
  position: relative;
  line-height: 160%;
  z-index: 1;
}
.subjectWrapper {
  width: 58px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-6xs-5);
  box-sizing: border-box;
}
.lineStrokeIcon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.frameDiv,
.frameGroup {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.frameDiv {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-16xl-2);
  min-width: 239px;
}
.frameGroup {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-4xl);
}
.message,
.typeYourMessege {
  position: relative;
  z-index: 1;
}
.message {
  width: 76px;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.typeYourMessege {
  line-height: 28.9px;
}
.textArea {
  height: 174.3px;
  width: 769px;
  position: relative;
  border-radius: var(--br-9xs);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  display: none;
  max-width: 100%;
}
.messageParent,
.typeYourMessegeParent {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.typeYourMessegeParent {
  flex: 1;
  border-radius: var(--br-9xs);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
  flex-direction: row;
  padding: var(--padding-3xl-2) var(--padding-xl) var(--padding-3xl);
  z-index: 2;
  font-size: var(--paragraph-size);
  color: var(--color-gray-200);
}
.messageParent {
  height: 198.6px;
  flex-direction: column;
  gap: var(--gap-8xs-3);
}
.primaryButtonText {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 119px;
}
.primaryButton {
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-black);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-mid-8) var(--padding-13xl) var(--padding-mid-9);
  white-space: nowrap;
  z-index: 1;
}
.primaryButtonWrapper {
  width: 747px;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-snow);
}
.contactUsInner,
.frameParent,
.primaryButtonWrapper {
  display: flex;
  align-items: flex-start;
  max-width: 100%;
}
.frameParent {
  width: 768px;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-16xl-4);
}
.contactUsInner {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-5xl-2) var(--padding-19xl);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-gray-300);
  font-family: var(--h3);
}
@media screen and (max-width: 450px) {
  .frameContainer,
  .frameDiv,
  .frameParent {
    gap: var(--gap-lg);
  }
}
