.join {
  margin: 0;
  position: relative;
  font-size: inherit;
  line-height: 140%;
  font-weight: 700;
  font-family: inherit;
  white-space: nowrap;
  display: inline-block;
  min-width: 78px;
}
.loginsignin {
  width: 100%;
  position: relative;
  border-radius: 8px;
  background-color: var(--color-white);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-29xl) var(--padding-29xl) 117.1px;
  box-sizing: border-box;
  gap: 75px;
  line-height: normal;
  letter-spacing: normal;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--primary-text);
  font-family: var(--h3);
}
@media screen and (max-width: 450px) {
  .loginsignin {
    gap: var(--gap-18xl);
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
