.primaryButtonText {
  position: relative;
  font-weight: 500;
}
.primaryButton,
.primaryButtonWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.primaryButton {
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-black);
  justify-content: flex-start;
  padding: var(--padding-7xs) var(--padding-13xl);
  z-index: 3;
}
.primaryButtonWrapper {
  width: 1212px;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
}
.passwordIcon,
.rockoShely {
  position: relative;
  z-index: 3;
}
.passwordIcon {
  height: 77.2px;
  width: 80px;
  border-radius: 50%;
  object-fit: cover;
}
.rockoShely {
  margin: 0;
  font-size: inherit;
  line-height: 45.3px;
  font-weight: 400;
  font-family: inherit;
}
.passwordParent,
.rockoShelyWrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.rockoShelyWrapper {
  flex-direction: column;
  padding: var(--padding-6xs-8) 0 0;
}
.passwordParent {
  flex-direction: row;
  gap: var(--gap-2xs);
}
.eparrowDownBoldIcon {
  width: 24px;
  height: 23.2px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  object-fit: contain;
  z-index: 3;
}
.eparrowDownBoldWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-8xs-8) 0 0;
}
.countryRanking {
  margin: 0;
  position: relative;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  z-index: 3;
}
.componentIcon {
  width: 46px;
  height: 19.3px;
  position: relative;
  z-index: 3;
}
.componentWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-7xl-1) 0 0;
}
.countryRankingParent {
  flex: 1;
  display: flex;
  flex-direction: row;
  gap: var(--gap-7xs);
  min-width: 224px;
  max-width: 100%;
}
.countryRankingParent,
.frameContainer,
.frameWrapper {
  align-items: flex-start;
  justify-content: flex-start;
}
.frameContainer {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  gap: var(--gap-sm);
  max-width: 100%;
}
.frameWrapper {
  width: 382px;
  flex-direction: column;
  padding: var(--padding-6xs-8) 0 0;
  box-sizing: border-box;
}
.frameGroup,
.frameParent,
.frameWrapper {
  display: flex;
  max-width: 100%;
}
.frameGroup {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  gap: var(--gap-xl);
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
}
.frameParent {
  width: 1230px;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-sm-5);
}
.dashboardInner {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) 0 var(--padding-29xl);
  box-sizing: border-box;
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-snow);
  font-family: var(--h3);
}
@media screen and (max-width: 1200px) {
  .dashboardInner {
    padding-left: var(--padding-5xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .primaryButtonText {
    font-size: var(--font-size-10xl);
  }
  .rockoShely {
    line-height: 36px;
  }
  .countryRanking,
  .rockoShely {
    font-size: var(--font-size-13xl);
  }
}
@media screen and (max-width: 750px) {
  .frameGroup {
    flex-wrap: wrap;
    justify-content: center;
  }
}
@media screen and (max-width: 450px) {
  .primaryButtonText {
    font-size: var(--font-size-3xl);
  }
  .rockoShely {
    line-height: 27px;
  }
  .countryRanking,
  .rockoShely {
    font-size: var(--font-size-5xl);
  }
  .countryRankingParent,
  .frameContainer {
    flex-wrap: wrap;
  }
}
