.i,
.largerthan {
  position: absolute;
  top: 50%;
  line-height: 44px;
  display: none;
  white-space: nowrap;
}
.largerthan {
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.i {
  height: 102.5%;
  font-size: var(--font-size-6xl);
  font-family: var(--font-courgette);
  width: 11.1px;
}
.largerthanParent {
  height: 40px;
  width: 0;
  position: relative;
  display: none;
  font-size: var(--font-size-5xl);
}
.home {
  align-self: stretch;
  position: relative;
  color: var(--color-black);
}
.aboutUs,
.whatWeDo {
  white-space: nowrap;
}
.aboutUs,
.contact,
.media,
.whatWeDo {
  align-self: stretch;
  position: relative;
  font-weight: 500;
}
.navLink {
  height: 51px;
  display: none;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
  gap: var(--gap-xl);
  color: var(--secondary-text);
}
.navbarButtonText {
  align-self: stretch;
  position: relative;
  font-weight: 500;
}
.navButton,
.navbar {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: flex-start;
}
.navButton {
  height: 43px;
  border-radius: var(--br-9xs);
  background-color: var(--primary-text);
  display: none;
  align-items: flex-start;
  padding: var(--padding-xs) var(--padding-12xl);
  text-align: right;
  color: var(--color-white);
}
.navbar {
  align-self: stretch;
  width: 1500px;
  backdrop-filter: blur(30px);
  background-color: var(--color-darkolivegreen-100);
  border-bottom: 1px solid var(--color-beige);
  display: flex;
  align-items: center;
  padding: var(--padding-lg) 0;
  gap: var(--gap-252xl);
  flex-shrink: 0;
  debug_commit: 1de1738;
  max-width: 102%;
  z-index: 4;
  text-align: left;
  font-size: var(--paragraph-size);
  color: var(--color-black);
  font-family: var(--h3);
}
.dashboard {
  margin: 0 !important;
  height: 47.8px;
  width: 303px;
  position: absolute;
  top: 17.7px;
  left: 0;
  font-size: inherit;
  line-height: 44px;
  font-weight: 700;
  font-family: inherit;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  white-space: nowrap;
  z-index: 5;
}
.primaryButtonText {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 74px;
  white-space: nowrap;
}
.navbarParent,
.primaryButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.primaryButton {
  margin: 0 !important;
  position: absolute;
  right: 113px;
  bottom: 17.2px;
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-black);
  padding: var(--padding-mid-8) var(--padding-13xl) var(--padding-mid-9);
  white-space: nowrap;
  cursor: pointer;
  z-index: 5;
  font-size: var(--font-size-lg);
  color: var(--color-snow);
}
.navbarParent {
  align-self: stretch;
  height: 91.9px;
  padding: 0 0 0 var(--padding-12xs);
  box-sizing: border-box;
  position: sticky;
  top: 0;
  z-index: 99;
  max-width: 100%;
  text-align: center;
  font-size: var(--font-size-17xl);
  color: var(--color-black);
  font-family: var(--h3);
}
