.image5Icon {
  width: 1272px;
  height: 430px;
  position: relative;
  object-fit: cover;
  display: none;
  max-width: 100%;
}
.contributeToMake {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
  z-index: 1;
}
.primaryButtonText {
  position: relative;
  font-weight: 500;
}
.primaryButton {
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-cadetblue-300);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-13xl);
  white-space: nowrap;
  z-index: 1;
}
.secondaryButtonText {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 76px;
}
.ctaInner,
.primaryButtonParent,
.secondaryButton {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.secondaryButton {
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-white);
  justify-content: flex-start;
  padding: var(--padding-base) var(--padding-13xl);
  z-index: 1;
  color: var(--primary-text);
}
.ctaInner,
.primaryButtonParent {
  justify-content: center;
  max-width: 100%;
}
.primaryButtonParent {
  gap: var(--gap-smi);
}
.ctaInner {
  width: 947px;
  text-align: right;
  font-size: var(--paragraph-size);
  color: var(--color-black);
}
.cta,
.ctaWrapper {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 100%;
}
.cta {
  flex: 1;
  flex-direction: column;
  align-items: flex-end;
  padding: var(--padding-34xl) var(--padding-146xl) var(--padding-104xl)
    var(--padding-124xl);
  gap: var(--gap-55xl);
  background-image: url(/public/image-5@2x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.ctaWrapper {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  padding: 0 0 0 var(--padding-5xs);
  text-align: center;
  font-size: var(--font-size-33xl);
  color: var(--color-mintcream);
  font-family: var(--h3);
}
@media screen and (max-width: 1200px) {
  .cta {
    padding-left: var(--padding-52xl);
    padding-right: var(--padding-63xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .contributeToMake {
    font-size: var(--font-size-23xl);
    line-height: 50px;
  }
}
@media screen and (max-width: 750px) {
  .primaryButtonParent {
    flex-wrap: wrap;
  }
  .cta {
    gap: var(--gap-18xl);
    padding-left: var(--padding-16xl);
    padding-right: var(--padding-22xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .contributeToMake {
    font-size: var(--font-size-12xl);
    line-height: 37px;
  }
  .cta {
    gap: var(--gap-lg);
    padding-top: var(--padding-15xl);
    padding-bottom: var(--padding-61xl);
    box-sizing: border-box;
  }
}
