.firstName {
  width: 107px;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.firstName,
.john,
.lineStrokeIcon {
  position: relative;
  z-index: 1;
}
.john {
  font-size: var(--paragraph-size);
  line-height: 26px;
  color: var(--primary-text);
  display: inline-block;
  min-width: 36px;
}
.lineStrokeIcon {
  align-self: stretch;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.firstNameParent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs-9);
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-gray-300);
  font-family: var(--h3);
}
