.weProvideA {
  margin: 0;
  height: 143px;
  flex: 1;
  position: relative;
  font-size: inherit;
  line-height: 120%;
  font-weight: 700;
  font-family: inherit;
  display: inline-block;
  max-width: 100%;
}
.sectionTitleIcon {
  height: 83.2px;
  width: 240.6px;
  position: absolute;
  margin: 0 !important;
  top: -68px;
  left: -24px;
  object-fit: contain;
  z-index: 1;
}
.weProvideAPlaceForPeoplesParent {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  max-width: 100%;
}
.blankLine,
.encouragingParticipationIn,
.maintainingCleanlinessNot {
  margin: 0;
}
.encouragingParticipationInContainer {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-5xl);
  line-height: 44px;
  color: var(--color-gray-400);
}
.frameGroup,
.frameWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.frameGroup {
  align-self: stretch;
  gap: var(--gap-5xl);
  flex-shrink: 0;
  debug_commit: 1de1738;
}
.frameWrapper {
  flex: 1;
  padding: 0 0 var(--padding-21xl);
  box-sizing: border-box;
  min-width: 452px;
  min-height: 647px;
}
.videoIcon {
  width: 483.2px;
  position: relative;
  max-height: 100%;
  flex-shrink: 0;
  debug_commit: 1de1738;
  max-width: 100%;
}
.frameParent,
.homePageInner {
  display: flex;
  flex-direction: row;
  max-width: 100%;
}
.frameParent {
  width: 1199px;
  align-items: flex-end;
  justify-content: flex-start;
  gap: var(--gap-lgi-8);
}
.homePageInner {
  width: 1365px;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl) var(--padding-51xl-5);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--h2-size);
  color: var(--primary-text);
  font-family: var(--h3);
}
@media screen and (max-width: 1050px) {
  .weProvideA {
    font-size: var(--font-size-19xl);
    line-height: 46px;
  }
  .videoIcon {
    flex: 1;
  }
  .frameParent {
    flex-wrap: wrap;
  }
  .homePageInner {
    padding-bottom: var(--padding-27xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .frameWrapper {
    min-width: 100%;
  }
}
@media screen and (max-width: 450px) {
  .weProvideA {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
  .encouragingParticipationInContainer {
    font-size: var(--font-size-lgi);
    line-height: 35px;
  }
  .homePageInner {
    padding-bottom: var(--padding-11xl);
    box-sizing: border-box;
  }
}
