.headerIcon {
  width: 1500px;
  height: 652px;
  position: absolute;
  margin: 0 !important;
  right: -24px;
  bottom: 195px;
}
.bg,
.contactDetailsIcon {
  position: relative;
  max-width: 100%;
}
.bg {
  height: 584px;
  width: 1500px;
  background-color: var(--color-snow);
  display: none;
  z-index: 0;
}
.contactDetailsIcon {
  width: 325px;
  max-height: 100%;
  z-index: 1;
}
.bb6dc73d558e36e30b6b9e0d476d33Icon {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  right: -3px;
  bottom: 0;
  object-fit: cover;
  z-index: 2;
}
.frameParent,
.header {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  box-sizing: border-box;
}
.header {
  flex: 1;
  background-color: var(--color-snow);
  flex-direction: row;
  justify-content: flex-end;
  padding: 188px 405px;
  position: relative;
  max-width: 102%;
  flex-shrink: 0;
}
.frameParent {
  height: 673px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0;
  gap: var(--gap-12xs-5);
  max-width: 100%;
}
.mediaChild {
  width: 1500px;
  height: 586px;
  position: absolute;
  margin: 0 !important;
  top: 85.5px;
  right: -24px;
  z-index: 3;
}
.e5f44f7c7e2f57a91a71982eb3ddafIcon {
  height: 313px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  z-index: 1;
}
.e5f44f7c7e2f57a91a71982eb3ddafWrapper,
.media {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.e5f44f7c7e2f57a91a71982eb3ddafWrapper {
  width: 528px;
  flex-direction: row;
  padding: 0 57px var(--padding-10xl);
  box-sizing: border-box;
  max-width: 100%;
}
.media {
  width: 100%;
  position: relative;
  background-color: var(--bg);
  overflow: hidden;
  flex-direction: column;
  gap: 101px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .header {
    padding-left: var(--padding-183xl);
    padding-right: var(--padding-183xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .header {
    padding-left: var(--padding-82xl);
    padding-right: var(--padding-82xl);
    box-sizing: border-box;
  }
  .e5f44f7c7e2f57a91a71982eb3ddafWrapper {
    padding-left: var(--padding-9xl);
    padding-right: var(--padding-9xl);
    box-sizing: border-box;
  }
  .media {
    gap: 50px;
  }
}
@media screen and (max-width: 450px) {
  .header {
    padding: 122px var(--padding-xl);
    box-sizing: border-box;
  }
  .media {
    gap: 25px;
  }
}
