.dashboardChild {
  width: 1356px;
  height: 533px;
  backdrop-filter: blur(40px);
  border-radius: var(--br-base);
  background: linear-gradient(
    111.03deg,
    rgba(239, 238, 238, 0.32),
    rgba(239, 238, 238, 0.06)
  );
  display: none;
  z-index: 0;
}
.bgIcon,
.dashboardChild,
.dashboardItem {
  position: relative;
  max-width: 100%;
}
.dashboardItem {
  width: 1286px;
  height: 89px;
  backdrop-filter: blur(4px);
  border-radius: var(--br-base);
  background: linear-gradient(
    111.03deg,
    rgba(239, 238, 238, 0.32),
    rgba(239, 238, 238, 0.06)
  );
  display: none;
  z-index: 1;
}
.bgIcon {
  height: 670.6px;
  flex: 1;
  overflow: hidden;
  z-index: 2;
}
.adminPortal {
  position: absolute;
  margin: 0 !important;
  top: 151.5px;
  left: 225px;
  line-height: 27px;
  display: inline-block;
  min-width: 83px;
  z-index: 4;
}
.bg,
.bgParent {
  position: absolute;
  left: 1px;
  width: 1500px;
}
.bgParent {
  margin: 0 !important;
  top: 654.2px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--h3);
}
.bg {
  height: 100%;
  top: 0;
  bottom: 0;
  background-color: var(--color-snow);
  z-index: 3;
}
.crazyFacts,
.currentScore {
  margin: 0;
  position: absolute;
  display: inline-block;
  z-index: 4;
}
.currentScore {
  top: 76.9px;
  left: 16px;
  font-size: inherit;
  line-height: 120%;
  font-style: italic;
  font-weight: 600;
  font-family: inherit;
  color: var(--primary-text);
  width: 318px;
  height: 42.6px;
  backdrop-filter: blur(4px);
  text-shadow: 1px 0 0#da3232, 0 1px 0#da3232, -1px 0 0#da3232, 0-1px 0#da3232;
}
.crazyFacts {
  top: 317.8px;
  left: 23px;
  font-size: var(--font-size-17xl);
  line-height: 44.7px;
  font-weight: 400;
  font-family: var(--font-righteous);
  width: 230px;
  height: 44.7px;
}
.image6Icon,
.image6Wrapper {
  position: absolute;
  overflow: hidden;
}
.image6Icon {
  width: calc(100% - 16px);
  top: 11px;
  right: 8px;
  left: 8px;
  max-width: 100%;
  height: 341px;
  object-fit: cover;
}
.image6Wrapper {
  top: 99.7px;
  left: 870px;
  background-color: #0b0a0a;
  width: 506px;
  height: 376.9px;
  z-index: 4;
}
.researchersEstimateThat {
  position: absolute;
  top: 404px;
  left: 23px;
  font-size: var(--font-size-xl);
  line-height: 120%;
  font-family: var(--font-revalia);
  display: inline-block;
  width: 607px;
  height: 72px;
  z-index: 4;
}
.bgGroup {
  align-self: stretch;
  height: 606.4px;
  position: relative;
  margin-top: -36.9px;
}
.dashboard,
.frameParent {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.frameParent {
  align-self: stretch;
  align-items: flex-end;
  max-width: 100%;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-black);
  font-family: var(--font-rosario);
}
.dashboard {
  width: 100%;
  position: relative;
  background-color: var(--bg);
  overflow: hidden;
  align-items: flex-start;
  padding: 0 0 141px;
  box-sizing: border-box;
  gap: 32.6px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1050px) {
  .currentScore {
    font-size: var(--font-size-13xl);
    line-height: 38px;
  }
  .crazyFacts {
    font-size: var(--font-size-10xl);
    line-height: 35px;
  }
}
@media screen and (max-width: 750px) {
  .dashboard {
    gap: var(--gap-base);
  }
}
@media screen and (max-width: 450px) {
  .adminPortal {
    font-size: var(--font-size-lgi);
    line-height: 22px;
  }
  .currentScore {
    font-size: var(--font-size-5xl);
    line-height: 29px;
  }
  .crazyFacts {
    font-size: var(--font-size-3xl);
    line-height: 26px;
  }
  .researchersEstimateThat {
    font-size: var(--paragraph-size);
    line-height: 19px;
  }
}
