.firstName {
  width: 78px;
  position: relative;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.lineStrokeIcon {
  align-self: stretch;
  height: 1px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
}
.emailId {
  width: 78px;
  position: relative;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.emailInput,
.firstNameLabel {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.emailInput {
  align-self: stretch;
  gap: var(--gap-14xl-3);
}
.firstNameLabel {
  flex: 1;
  gap: var(--gap-20xl-7);
  min-width: 166px;
}
.lastName {
  width: 90px;
  position: relative;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.lineStrokeIcon1 {
  align-self: stretch;
  height: 1px;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  flex-shrink: 0;
}
.firstNameLabelParent,
.lastNameInput {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.lastNameInput {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-20xl-7);
  min-width: 166px;
}
.firstNameLabelParent {
  align-self: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  gap: var(--gap-13xl);
}
.lineStrokeIcon2 {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.firstNameArea {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-20xl-8);
}
.password {
  width: 78px;
  position: relative;
  line-height: 160%;
  display: flex;
  align-items: center;
}
.firstNameField {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-11xl-2);
}
.lineStrokeIcon3 {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.firstNameInput {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-20xl-7);
}
.primaryButtonText {
  position: relative;
  font-weight: 500;
  display: inline-block;
  min-width: 56px;
}
.joinForm,
.nameInput,
.primaryButton {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.primaryButton {
  backdrop-filter: blur(80px);
  border-radius: var(--br-9xs);
  background-color: var(--color-cadetblue-100);
  flex-direction: row;
  padding: var(--padding-base-4) var(--padding-13xl) var(--padding-base-5);
  white-space: nowrap;
  cursor: pointer;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-black);
}
.joinForm,
.nameInput {
  max-width: 100%;
}
.nameInput {
  flex: 1;
  flex-direction: column;
  gap: var(--gap-68xl-3);
}
.joinForm {
  align-self: stretch;
  flex-direction: row;
  padding: 0 var(--padding-11xs) 0 var(--padding-12xs);
  box-sizing: border-box;
  text-align: left;
  font-size: var(--font-size-smi);
  color: var(--color-gray-300);
  font-family: var(--h3);
}
@media screen and (max-width: 550px) {
  .firstNameLabelParent {
    gap: var(--gap-base);
  }
  .firstNameArea {
    gap: var(--gap-xl);
  }
  .firstNameField {
    gap: var(--gap-mini);
  }
  .firstNameInput {
    gap: var(--gap-xl);
  }
  .nameInput {
    gap: var(--gap-25xl);
  }
}
@media screen and (max-width: 450px) {
  .emailInput {
    gap: var(--gap-mid);
  }
  .firstNameLabel,
  .lastNameInput {
    gap: var(--gap-xl);
  }
  .nameInput {
    gap: var(--gap-3xl);
  }
}
